.content{
	padding-top: 96px;
	padding-bottom: 80px;
}
.store-title{
	font-size: 48px;
	font-weight: bold;
	border-left: 8px solid #9F1213;
	margin-bottom: 33px;
	padding-left: 16px;
}
.select-list{
	margin-bottom: 41px;
	display: flex;
}
.select-list label{
	font-size: 24px;
	margin-right: 16px;
	font-weight: bold;
}
.select-list select{
	font-size: 18px;
	width: 304px;
}

.store-info{
	display: flex;
}

.Taipei-store{
	margin-right: 24px;
}

.store-contact h3{
	font-size: 24px;
	border:1px solid #00000020;
	padding:8px 0px 8px 24px;
}
.contact-info{
	padding:16px 0px 16px 24px;
	font-size: 20px;
	border: 1px solid #00000020 ;
}
.contact-info li:nth-child(1),.contact-info li:nth-child(2){
	margin-bottom: 8px;
}
.contact-info i{
	font-size: 18px;
	margin-right: 11px;
}
.contact-info a{
	color:black;
}
.contact-info a:hover{
	color: #AA0601;
}
@media (max-width:991px){
    .store-info{
		flex-direction: column;
	}
	.Taipei-store img{
		width: 50%;
		margin-right: 24px;
	}
	.Taipei-store{
		display: flex;
		margin-bottom: 24px;
	}
}
@media (max-width:768px){
    .Taipei-store{
		flex-direction: column;
		width: 100%;
	}
	.Taipei-store img{
		width: 100%;
		margin-right: 24px;
	}
	.store-title{
		font-size: 24px;
	}
	.select-list label{
		font-size: 16px;
		margin-right: 8px;	
	}
	.select-list select{
		width: 265px;
	}
}



